import jQuery from 'jquery';
import 'picturefill';
window.$ = window.jQuery = jQuery;

import HotDog from './components/HotDog';
import Wave from './components/Wave';
import ScrollBackground from './components/ScrollBackground';
import Cards from './components/Cards';
// import Mic from './components/Mic';

if ( !isMobile() ) {
  var SmoothScroll = require('smooth-scroll');
  var linear = new SmoothScroll('.site-container', {
    speed: 5000,
    easing: 'easeInOutQuad'
  });
  $('.hot-dog').each((index, element) => new HotDog(element));
  $('.wave-transition').each((index, element) => new Wave(element));
  $('.card').each((index, element) => new Cards(element));
  $('.scroll_background').each((index, element) => new ScrollBackground(element));
  // let mic = new Mic();
} else {
  $('.site-main').addClass('is-mobile');
  $('body').removeClass('noScroll');
  $('body').css('overflow','auto');
}


function isMobile() {
	return /Mobi/i.test(navigator.userAgent) || /Android/i.test(navigator.userAgent) || !(window.matchMedia("(min-width: " + 600 + "px)").matches);
}
