import $ from 'jquery';
import { S_IFIFO } from 'constants';
import Mic from './Mic';

const $window = $(window);

class HotDog {
  constructor(element) {
    this.$element = $(element);
    this.els = {
      component: this.$element,
      $textPath1: $('#hot-dog__svg__text-path-1'),
      $textPath2: $('#hot-dog__svg__text-path-2'),
      $textPath3: $('#hot-dog__svg__text-path-3'),
      $svgPath: $('.hot-dog__svg__path'),
      $jelly: $('.hot-dog__jelly'),
      $hotDogCTA: $('.arrow-container'),
      $toggleMic: $('#toggleMic')
    };
    this.state = {
      windowWidth: $window.width(),
      windowHeight: $window.height(),
      mouseX: null,
      mouseY: null,
      offsetLeft: 0,
      offsetLeftSpeed: 0,
      currentOffsetLeft: 0,
      offsetRight: 0,
      offsetRightSpeed: 0,
      currentOffsetRight: 0,
      scale: 0,
      scaleSpeed: 0,
      currentScale: 0,
      drag: 0.6,
      jellyMode: false,
      micRMS: 0,
      mic: false
    }
    this.options = {
      minStartOffset: 11,
      maxStartOffset: 35,
      acceleration: 0.15,
      defaultDrag: 0.6,
      jellyDrag: 0.95
    }
    this.ctaTimeout = "";

    this.addListeners();
    this.animate();
  }

  addListeners() {
    $window.on('mousemove', event => this.handleMouseMove(event));
    this.els.$jelly.on('click', this.handleWindowClick.bind(this));
    this.state.micRMS = new Mic();
    this.els.$toggleMic.on('click', this.handleToggleMic.bind(this))
    $("#tshirt-click").on('click', function(e) {
      $('.purchase').removeClass('inactive');
      $('.tshirt').addClass('inactive');
    })
    $("#tshirt-click-2").on('click', function(e) {
      $('.tshirt').removeClass('inactive');
      $('.purchase').addClass('inactive');
    })
  }

  handleToggleMic(e){
    if(this.state.mic){
      this.state.mic = false;
      $('.scroll_background').show()
    } else {
      this.state.mic = true;
      $('.scroll_background').hide()
    }
  }

  handleMouseMove(event) {
    this.state.mouseX = event.clientX;
    this.state.mouseY = event.clientY;
  }

  handleWindowClick() {
    this.options.drag = 0.95;
    const jellyState = this.state.jellyMode;
    if (jellyState){
      this.state.drag = this.options.defaultDrag;
      this.state.jellyMode = false;
    } else {
      this.state.drag = this.options.jellyDrag;
      this.state.jellyMode = true;
      this.calculatePositions(-2000, -2000);
      this.render();
    }
    this.els.not('.ready').$jelly.toggleClass('hot-dog__jelly--jelly-mode', this.state.jellyMode);
  }

  calculatePositions(mouseX, mouseY) {
    const mousePercentageLeft = mouseX / this.state.windowWidth * 100;
    const mousePercentageRight = (this.state.windowWidth - mouseX) / this.state.windowWidth * 100;
    const mousePercentageTop = mouseY / this.state.windowHeight * 100;

    // use line graph to work out this formula y = .24x + 11
    this.state.offsetLeft = .24 * mousePercentageLeft + 11;
    this.state.offsetLeftSpeed += (this.state.offsetLeft - this.state.currentOffsetLeft) * this.options.acceleration;
    this.state.offsetLeftSpeed *= this.state.drag;
    this.state.currentOffsetLeft += this.state.offsetLeftSpeed;

    // use line graph to work out this formula y = .24x + 11
    this.state.offsetRight = .24 * mousePercentageRight + 11;
    this.state.offsetRightSpeed += (this.state.offsetRight - this.state.currentOffsetRight) * this.options.acceleration;
    this.state.offsetRightSpeed *= this.state.drag;
    this.state.currentOffsetRight += this.state.offsetRightSpeed;

    // use line graph to work out this formula y = .03x - 1.5
    this.state.scale = (mousePercentageTop * 0.03) - 1.5;
    this.state.scaleSpeed += (this.state.scale - this.state.currentScale) * this.options.acceleration;
    this.state.scaleSpeed *= this.state.drag;
    this.state.currentScale += this.state.scaleSpeed;
  }

  render() {
    this.els.$textPath1.attr('startOffset', `${this.state.currentOffsetLeft}%`);
    this.els.$textPath2.attr('startOffset', `${this.state.currentOffsetRight}%`);
    this.els.$textPath3.attr('startOffset', `${this.state.currentOffsetLeft}%`);
    this.els.$svgPath.attr('transform', `scale(1, ${this.state.currentScale})`);
  }

  animate() {
    // console.log(this.state.mic);
    if(this.state.mic){

      let freq = this.state.micRMS.spectrum[0] / 128;
      let vol = this.state.micRMS.volume / 128;
      // console.log(freq * ($window.height()/2))
      // console.log(ethis.state.mouseY)
      if(this.state.mouseX && this.state.micRMS.volume){
        // console.log(vol)
        // console.log(freq)
        // console.log(this.state.micRMS.spectrum[0])
        // console.log(vol * ($window.height()/2));
        this.calculatePositions((freq * ($window.width()/2)) , (freq * ($window.height()/2)));
        this.render();
      }
    } else {
      if(this.state.mouseX && this.state.mouseY){
        this.calculatePositions(this.state.mouseX, this.state.mouseY);
        this.render();
      }
    }
    window.requestAnimationFrame(this.animate.bind(this));
  }
}

export default HotDog;
